:root {
  --tile-size: min(calc((100vw - 1rem * 2) / 8), calc((100vh - 6rem * 2) / 8));
}

body {
  height: 100vh;
  background: url('../sprites/background.jpg');
  background-position: center;
  background-size: cover;
  // font-family: Comic Neue, cursive;
  font-family: 'Saira Semi Condensed', sans-serif;

  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: end;
  flex-direction: column;

  gap: 2rem;

  box-sizing: border-box;
  overflow: hidden;
  margin: 0;
  padding: {
    left: 1rem;
    right: 1rem;
    top: max(3rem, env(safe-area-inset-top));
    bottom: max(3rem, env(safe-area-inset-bottom));
  }
}

.statistics {
  color: #ecffff;
  width: 100%;
  max-width: calc(var(--tile-size, 8em) * 8);
  font-size: 1.5rem;
  font-weight: bold;
  text-shadow: -3px 3px #19253da3;

  & > ul {
    margin: 0;
    padding: 0;
    list-style: none;

    display: flex;
    justify-content: space-between;
  }
}

.container {
  position: relative;

  .multiplier,
  .game-over {
    position: absolute;
    pointer-events: none;

    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    margin: 0;
    opacity: 0;

    display: flex;
    align-items: center;
    justify-content: center;

    font-size: 4rem;

    color: #fcd53f;
    text-shadow: -4px 4px 4px #3a3a3a;
  }

  .game-over {
    cursor: pointer;
    background-color: #34312f82;
    box-shadow: inset -4px 4px 18px #4a4a4a;
    border-radius: 1rem;
    transition: opacity 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  }

  .multiplier {
    font-size: 15rem;
  }
}

.field {
  width: var(--field-width, 8em);
  height: var(--field-height, 8em);

  font-size: var(--tile-size, 8em);
  position: relative;
  border-radius: 0.5rem;
  border: 0.5rem double #21201e85;
  background-color: rgb(47 79 79 / 60%);
  box-shadow: inset -4px 4px 18px 0px #4a4a4a;

  .tile {
    // make tile easier to select
    outline: 8px solid #0000;

    border-color: #98dab0;

    width: 1em;
    height: 1em;
    position: absolute;

    display: flex;
    align-items: center;
    justify-content: center;

    box-sizing: border-box;

    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    text-shadow: -2px 4px 8px #525252;

    &.active {
      outline: 5px ridge #e719199e;
      z-index: 1;
    }

    cursor: pointer;

    &::before {
      // font-size: 0.7em;
      content: '';
      width: 1em;
      height: 1em;
      background-size: cover;
    }

    $tiles: (
      candy: 'üç¨',
      lollypop: 'üç≠',
      chocolate: 'üç´',
      poop: 'üí©',
      beacon: 'ü•©',
      dice: 'üé≤',
    );

    // @each $name, $tile in $tiles {
    //   &.#{$name}:before {
    //     content: $tile;
    //   }
    // }

    &.candy:before {
      background-image: url('../sprites/crystal.png');
    }

    &.lollypop:before {
      background-image: url('../sprites/mushroom.png');
    }

    &.chocolate:before {
      background-image: url('../sprites/pumpikin.png');
    }

    &.poop:before {
      background-image: url('../sprites/scull.png');
    }

    &.beacon:before {
      background-image: url('../sprites/stone.png');
    }

    &.dice:before {
      background-image: url('../sprites/hat.png');
    }

    &.flask:before {
      background-image: url('../sprites/flask.png');
    }
  }
}
