:root {
  --tile-size: min(calc((100vw - 1rem * 2)  / 8), calc((100vh - 6rem * 2)  / 8) );
}

body {
  height: 100vh;
  box-sizing: border-box;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: max(3rem, env(safe-area-inset-top) );
  padding-bottom: max(3rem, env(safe-area-inset-bottom) );
  background: url("background.fd88afd0.jpg") center / cover;
  flex-direction: column;
  justify-content: end;
  align-items: center;
  gap: 2rem;
  margin: 0;
  font-family: Saira Semi Condensed, sans-serif;
  font-weight: bold;
  display: flex;
  overflow: hidden;
}

.statistics {
  color: #ecffff;
  width: 100%;
  max-width: calc(var(--tile-size, 8em)  * 8);
  text-shadow: -3px 3px #19253da3;
  font-size: 1.5rem;
  font-weight: bold;
}

.statistics > ul {
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.container {
  position: relative;
}

.container .multiplier, .container .game-over {
  pointer-events: none;
  opacity: 0;
  color: #fcd53f;
  text-shadow: -4px 4px 4px #3a3a3a;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-size: 4rem;
  display: flex;
  position: absolute;
  inset: 0;
}

.container .game-over {
  cursor: pointer;
  background-color: #34312f82;
  border-radius: 1rem;
  transition: opacity .3s cubic-bezier(.4, 0, .2, 1);
  box-shadow: inset -4px 4px 18px #4a4a4a;
}

.container .multiplier {
  font-size: 15rem;
}

.field {
  width: var(--field-width, 8em);
  height: var(--field-height, 8em);
  font-size: var(--tile-size, 8em);
  background-color: #2f4f4f99;
  border: .5rem double #21201e85;
  border-radius: .5rem;
  position: relative;
  box-shadow: inset -4px 4px 18px #4a4a4a;
}

.field .tile {
  width: 1em;
  height: 1em;
  box-sizing: border-box;
  text-shadow: -2px 4px 8px #525252;
  cursor: pointer;
  border-color: #98dab0;
  outline: 8px solid #0000;
  justify-content: center;
  align-items: center;
  transition: all .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
}

.field .tile.active {
  z-index: 1;
  outline: 5px ridge #e719199e;
}

.field .tile:before {
  content: "";
  width: 1em;
  height: 1em;
  background-size: cover;
}

.field .tile.candy:before {
  background-image: url("crystal.18a77376.png");
}

.field .tile.lollypop:before {
  background-image: url("mushroom.c16d5ba6.png");
}

.field .tile.chocolate:before {
  background-image: url("pumpikin.12766bca.png");
}

.field .tile.poop:before {
  background-image: url("scull.723562c5.png");
}

.field .tile.beacon:before {
  background-image: url("stone.0f04f26a.png");
}

.field .tile.dice:before {
  background-image: url("hat.3efde180.png");
}

.field .tile.flask:before {
  background-image: url("flask.6c9424bf.png");
}

